
import { defineComponent, ref, watchEffect } from "vue";
import { useRouter } from "vue-router";

import FilterTag from "@/components/FilterTag.vue";
import CollectionCard from "@/components/CollectionCard.vue";
import CollapseItem from "@/components/CollapseItem.vue";

import {
  closeAllTags,
  closeTag,
  watchFilterOptions
} from "@/utils/asidtFilter";
import { FilterOption, OptionItem } from "@/typings/asideFilter";
import { CollectionCreateInputs } from "@/typings/collection";

const statusOptions: OptionItem[] = [
  { label: "Public", value: "public", checked: false },
  { label: "Private", value: "private", checked: false }
];
const yearOptions: OptionItem[] = [
  { label: "2021", value: "2021", checked: false },
  { label: "2020", value: "2020", checked: false }
];
const seasonOptions: OptionItem[] = [
  { label: "Spring", value: "spring", checked: false },
  { label: "Summer", value: "summer", checked: false },
  { label: "Fall", value: "fall", checked: false },
  { label: "Winter", value: "winter", checked: false }
];

const Component = defineComponent({
  name: "Collections",
  components: {
    FilterTag,
    CollectionCard,
    CollapseItem
  },
  setup: function() {
    const router = useRouter();
    const filterOptions = ref<FilterOption[]>([
      {
        title: "Color",
        items: [
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false }
        ]
      },
      {
        title: "Year",
        items: [
          { label: "Spring", value: false, checked: false },
          { label: "Summer", value: false, checked: false },
          { label: "Fall", value: false, checked: false },
          { label: "Winter", value: false, checked: false }
        ]
      },
      {
        title: "Season",
        items: [
          { label: "33333", value: false, checked: false },
          { label: "444444", value: false, checked: false }
        ]
      },
      {
        title: "Theme",
        items: [
          { label: "555555", value: false, checked: false },
          { label: "666666", value: false, checked: false }
        ]
      }
    ]);

    const tags = ref<string[]>([]);
    const hasAside = ref(true);
    const hasOpenButton = ref(false);
    const colorIndex = ref(-1);
    const searchContent = ref("");
    const dialogCreateVisible = ref(false);
    const form = ref<CollectionCreateInputs>({
      status: "private",
      name: "",
      owner: "",
      description: "",
      year: "",
      season: "",
      brand: "",
      sport: "",
      theme: "",
      hashtag: ""
    });

    watchEffect(() => {
      tags.value = watchFilterOptions(filterOptions.value);
    });

    const onHideButtonClick = (value: boolean) => {
      hasAside.value = value;

      if (!value) {
        setTimeout(() => {
          hasOpenButton.value = true;
        }, 300);
      } else {
        hasOpenButton.value = false;
      }
    };
    const onCreateClick = () => {
      dialogCreateVisible.value = true;
    };
    const onCreateConfirm = () => {
      console.log("create");
      dialogCreateVisible.value = false;
    };
    const onAnalysisClick = () => {
      router.push({ name: "CollectionsAnalysis" });
    };
    const onColorClick = (index: number) => {
      colorIndex.value = index;
    };
    const onCloseTag = (tag: string) => {
      filterOptions.value = closeTag(filterOptions.value, tag);
    };
    const onCloseAllTags = () => {
      filterOptions.value = closeAllTags(filterOptions.value);
    };

    return {
      filterOptions,
      tags,
      hasAside,
      hasOpenButton,
      searchContent,
      colorIndex,
      dialogCreateVisible,
      statusOptions,
      yearOptions,
      seasonOptions,
      form,

      onHideButtonClick,
      onColorClick,
      onCreateClick,
      onCreateConfirm,
      onAnalysisClick,
      onCloseTag,
      onCloseAllTags
    };
  }
});

export default Component;
