<template>
  <el-container id="components-dashboard-component">
    <!--Aside Filter-->
    <el-aside class="aside" :class="{ 'aside-active': hasAside }">
      <div class="search-container">
        <el-input
          class="search-input"
          v-model="searchContent"
          placeholder="Search Fabric"
          clearable
        ></el-input>
        <div class="hide-button-container" @click="onHideButtonClick(false)">
          <p>Hide</p>
          <img
            class="hide-icon"
            src="@/assets/images/icons/png/switch.png"
            alt=""
          />
        </div>
      </div>
      <CollapseItem
        v-for="(option, index) in filterOptions"
        :key="index"
        :title="option.title"
      >
        <template v-if="option.title === 'Color'">
          <el-row class="colors-container" :gutter="5">
            <el-col
              class="color-wrapper"
              v-for="(item, _index) in option.items"
              :key="_index"
              :span="4"
              @click="onColorClick(_index)"
            >
              <div
                class="color-circle"
                :class="{ 'color-active': colorIndex === _index }"
                :style="{ background: item.label }"
              ></div>
            </el-col>
          </el-row>
        </template>
        <template v-else>
          <div
            v-for="(item, _index) in option.items"
            :key="_index"
            class="option"
          >
            <input type="checkbox" v-model="item.value" />
            <div class="name">{{ item.label }}</div>
          </div>
        </template>
      </CollapseItem>
    </el-aside>

    <div
      v-if="hasOpenButton"
      class="open-aside-button"
      @click="onHideButtonClick(true)"
    >
      <img
        class="arrow-right-icon"
        src="@/assets/images/icons/png/arrow-right.png"
        alt=""
      />
    </div>

    <!--Fabric Cards-->
    <el-main class="main-container">
      <div class="console-container">
        <el-button type="success" @click="onCreateClick">
          Create Collection
        </el-button>
        <el-button type="info" @click="onAnalysisClick">
          Analysis
        </el-button>
      </div>
      <div v-if="tags.length" class="tags-container">
        <FilterTag
          v-for="(tag, index) in tags"
          :key="index"
          :data="tag"
          @close="onCloseTag"
        ></FilterTag>
        <p class="button-clear-all-tags" @click="onCloseAllTags">Clear All</p>
      </div>
      <el-row :gutter="30">
        <el-col
          class="col"
          v-for="index in 10"
          :key="index"
          :span="hasAside ? 8 : 6"
        >
          <CollectionCard has-buttons></CollectionCard>
        </el-col>
      </el-row>
    </el-main>

    <!--Create Dialog-->
    <el-dialog
      title="Create Collection"
      v-model="dialogCreateVisible"
      width="40%"
      top="45px"
    >
      <div class="inputs-container">
        <el-row :gutter="30">
          <el-col :span="24">
            <el-select v-model="form.status">
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Collection Name</p>
            <el-input v-model="form.name" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Owner</p>
            <el-input v-model="form.owner" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Description (optional)</p>
            <el-input v-model="form.description" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <el-row :gutter="40">
              <el-col class="nmb" :span="12">
                <p class="label">Year (optional)</p>
                <el-select v-model="form.year">
                  <el-option
                    v-for="item in yearOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col class="nmb" :span="12">
                <p class="label">Season (optional)</p>
                <el-select v-model="form.season">
                  <el-option
                    v-for="item in seasonOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Brand (optional)</p>
            <el-input v-model="form.brand" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Sport (optional)</p>
            <el-input v-model="form.sport" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Theme (optional)</p>
            <el-input v-model="form.theme" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Hashtag (optional)</p>
            <el-input v-model="form.hashtag" clearable> </el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogCreateVisible = false"
            >Cancel</el-button
          >
          <el-button type="success" @click="onCreateConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </el-container>
</template>

<script lang="ts">
import { defineComponent, ref, watchEffect } from "vue";
import { useRouter } from "vue-router";

import FilterTag from "@/components/FilterTag.vue";
import CollectionCard from "@/components/CollectionCard.vue";
import CollapseItem from "@/components/CollapseItem.vue";

import {
  closeAllTags,
  closeTag,
  watchFilterOptions
} from "@/utils/asidtFilter";
import { FilterOption, OptionItem } from "@/typings/asideFilter";
import { CollectionCreateInputs } from "@/typings/collection";

const statusOptions: OptionItem[] = [
  { label: "Public", value: "public", checked: false },
  { label: "Private", value: "private", checked: false }
];
const yearOptions: OptionItem[] = [
  { label: "2021", value: "2021", checked: false },
  { label: "2020", value: "2020", checked: false }
];
const seasonOptions: OptionItem[] = [
  { label: "Spring", value: "spring", checked: false },
  { label: "Summer", value: "summer", checked: false },
  { label: "Fall", value: "fall", checked: false },
  { label: "Winter", value: "winter", checked: false }
];

const Component = defineComponent({
  name: "Collections",
  components: {
    FilterTag,
    CollectionCard,
    CollapseItem
  },
  setup: function() {
    const router = useRouter();
    const filterOptions = ref<FilterOption[]>([
      {
        title: "Color",
        items: [
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false },
          { label: "red", value: false, checked: false }
        ]
      },
      {
        title: "Year",
        items: [
          { label: "Spring", value: false, checked: false },
          { label: "Summer", value: false, checked: false },
          { label: "Fall", value: false, checked: false },
          { label: "Winter", value: false, checked: false }
        ]
      },
      {
        title: "Season",
        items: [
          { label: "33333", value: false, checked: false },
          { label: "444444", value: false, checked: false }
        ]
      },
      {
        title: "Theme",
        items: [
          { label: "555555", value: false, checked: false },
          { label: "666666", value: false, checked: false }
        ]
      }
    ]);

    const tags = ref<string[]>([]);
    const hasAside = ref(true);
    const hasOpenButton = ref(false);
    const colorIndex = ref(-1);
    const searchContent = ref("");
    const dialogCreateVisible = ref(false);
    const form = ref<CollectionCreateInputs>({
      status: "private",
      name: "",
      owner: "",
      description: "",
      year: "",
      season: "",
      brand: "",
      sport: "",
      theme: "",
      hashtag: ""
    });

    watchEffect(() => {
      tags.value = watchFilterOptions(filterOptions.value);
    });

    const onHideButtonClick = (value: boolean) => {
      hasAside.value = value;

      if (!value) {
        setTimeout(() => {
          hasOpenButton.value = true;
        }, 300);
      } else {
        hasOpenButton.value = false;
      }
    };
    const onCreateClick = () => {
      dialogCreateVisible.value = true;
    };
    const onCreateConfirm = () => {
      console.log("create");
      dialogCreateVisible.value = false;
    };
    const onAnalysisClick = () => {
      router.push({ name: "CollectionsAnalysis" });
    };
    const onColorClick = (index: number) => {
      colorIndex.value = index;
    };
    const onCloseTag = (tag: string) => {
      filterOptions.value = closeTag(filterOptions.value, tag);
    };
    const onCloseAllTags = () => {
      filterOptions.value = closeAllTags(filterOptions.value);
    };

    return {
      filterOptions,
      tags,
      hasAside,
      hasOpenButton,
      searchContent,
      colorIndex,
      dialogCreateVisible,
      statusOptions,
      yearOptions,
      seasonOptions,
      form,

      onHideButtonClick,
      onColorClick,
      onCreateClick,
      onCreateConfirm,
      onAnalysisClick,
      onCloseTag,
      onCloseAllTags
    };
  }
});

export default Component;
</script>

