<template>
  <div id="fabric-card-component">
    <div class="cover-images-container" @click="onImageClick">
      <div class="cover-images-wrapper">
        <el-row>
          <el-col v-for="index in 4" :key="index" :span="12">
            <div class="cover-image"></div>
          </el-col>
        </el-row>
      </div>
    </div>

    <div class="content-container">
      <div
        class="content-wrapper"
        :style="{ width: hasButtons ? '70%' : 'auto' }"
      >
        <h3 class="title">Personal Favorite</h3>
      </div>
      <div v-if="hasButtons" class="buttons-group">
        <div class="button-wrapper" @click="onDuplicateClick">
          <img
            class="icon"
            src="@/assets/images/icons/png/duplicate.png"
            alt=""
          />
          <p class="icon-name">Duplicate</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs } from "vue";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "CollectionCard",
  props: {
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { hasButtons } = toRefs(props);

    const onImageClick = () => {
      router.push({ name: "CollectionDetail", params: { id: "id" } });
    };

    const onDuplicateClick = () => {
      if (!hasButtons) return;
      alert("duplicate");
    };

    return {
      onImageClick,
      onDuplicateClick
    };
  }
});

export default Component;
</script>

